<template>
  <div class="main">
    <div class="wrapper">
      <nav class="nav">
        <div class="logo">xme<span style="color: #939393;">di</span><span style="color: #bdbdbd;">a</span></div>
        <a class="burger-nav" id="drawer">&#9776;</a>
        <ul>
          <li class="right"><a v-on:click="logout()" href="">Logout</a></li>
          <li class="right"><div class="vertical_hr"></div></li>
          <li class="right"><router-link to="/tvshows">TV shows</router-link></li>
          <li class="right"><router-link class="active" to="/movies">Movies</router-link></li>
          <li class="right"><router-link to="/">Homepage</router-link></li>
        </ul>
      </nav>
    </div>
    <!-- INFO MODAL -->
    <div id="wrapper" class="remodal-bg modal">
      <div data-remodal-id="modal" class="modal" role="dialog" aria-labelledby="modal1Title" aria-describedby="modal1Desc">
        <div>
          <h3 id="modal1Title">{{ modal_video.title }}</h3>
          <p id="modal1Desc">
          {{ modal_video.description }}
          <br>
          <div class="video-info">
            <p><span class="sub">Genre: </span> {{ modal_video.genre }}</p>
            <hr>
            <p><span class="sub">Director: </span> {{ modal_video.director }}</p>
            <hr>
            <p><span class="sub">Duration: </span> {{ modal_video.duration }}</p>
            <hr>
            <p v-if="modal_video.rate != null"><span class="sub">Rate: </span> {{ modal_video.rate.toFixed(2)}}</p>
            <hr>
            <p><span class="sub">Release Date: </span> {{ modal_video.release_date}}</p>
            <hr>
          </div>
          </p>
        </div>
        <br>
        <button v-if="!modal_video.series_id" class="modal-button" @click="openVideoModal()">Play</button>
      </div>
    </div>
    <!-- VIDEO MODAL -->
    <div id="video-modal" class="remodal-bg modal" v-if="modal_video != undefined">
      <div data-remodal-id="video-modal" class="modal" role="dialog" aria-labelledby="video-modal-title">
        <div>
          <h3 id="video-modal-title">{{ modal_video.title }}</h3>
          <video controls crossorigin playsinline width="600" class="ui image" id="api-video" :src="'http://192.168.1.148:8000/movies/' + modal_video.file_name" v-if="modal_video != undefined">
            <source :src="'http://192.168.1.148:8000/movies/' + modal_video.file_name" type="video/mp4" size="576" v-if="modal_video != undefined">
            <track v-if='checkSubtitles(modal_video.file_name)' kind="captions" label="Polish" srclang="pl" :src="'http://192.168.1.148:8000/subtitles/' + modal_video.file_name" default>
          </video>
          <div v-if="modal_video.file_name">
            Subtitles:
            <div v-if='modal_video.file_name.includes("NSB")'>
              <input v-model="video_subtitles" type="checkbox">
            </div>
          </div>
        </div>
        <br>
      </div>
    </div>
    <!-- MOVIE LIST -->
    <div class="container">
      <div v-if="movies.length > 0" v-for="i in Math.ceil(movies.length / 3)" class="row">
        <div class="four columns" v-for="movie in movies.slice((i - 1) * 3, i * 3)">
          <div class="card image" data-aos="fade-up" @click="setModal(movie)">
            <img :src="movie.poster_path" class="u-max-full-width">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Homepage',
  data () {
    return {
      movies: [],

      modal_video: {},
      video_subtitles: false
    }
  },
  mounted() {
    this.getMovies()
    var width = window.matchMedia("(max-width: 480px)")
    this.setDrawer(width)
    width.addListener(this.setDrawer)
    AOS.init({
      duration: 1200
    });
  },
  beforeCreate: function() {
    if (!this.$session.exists()) {
      this.$router.push('/login')
    }
  },
  methods: {
    setModal: function(video) {
      if (video.series_id) {
        this.getTvShowEpisodes(video.dir_name)
      }
      this.modal_video = video
      var inst = $('[data-remodal-id=modal]').remodal();
      inst.open()
    },
    checkSubtitles: function(title) {
      if (title == undefined || title == null) {
        return false
      }
      if (title.includes("NSB")) {
        return true
      }
      return false
    },
    openVideoModal: function() {
      var inst = $('[data-remodal-id=video-modal]').remodal()
      inst.open()
    },
    getMovies: function() {
      this.$http.get("http://192.168.1.148:8000/api/movies")
        .then((res) => {
          console.log(res.body)
          this.movies = res.body
        })
        .catch((err) => {
          console.log("err", err)
        })
    },
    logout: function() {
      this.$session.destroy()
      this.$router.push("/")
    },
    setDrawer: function(width) {
      var drawerHTML = document.getElementById("drawer")
      if (width.matches) {
        drawerHTML.innerHTML= "&#9776;";
      } else {
        drawerHTML.innerHTML = "";
      }
      $(".burger-nav").on("click", function(){
        $("nav ul").toggleClass("open");
      });
    },

  }
}
</script>

<style scoped>

.image {
  position: relative;
}

.image img {
  width: 100%;
  vertical-align: top;
}

.image:after, .image:before {
  cursor: pointer;
  position: absolute;
  opacity: 0;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
}

.image:after {
  content: '\A';
  width: 100%; height:100%;
  top: 0; left:0;
  background: rgba(0,0,0,0.7);
}

.image:before {
  font-family: "Roboto", sans-serif;
  font-size: 28px;
  font-weight: 300;
  content: "Show details";
  width: 100%;
  color: #fff;
  z-index: 1;
  bottom: 50%;;
  text-align: center;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.image:hover:after, .image:hover:before {
  opacity: 1;
}

</style>



