import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage'
import Movies from '@/components/Movies'

import Login from '@/components/Login'

Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage
    },
    {
      path: '/movies',
      name: 'Movies',
      component: Movies
    },
    {
      path: '/login',
      name: "Login",
      component: Login
    }
  ]
})



