<template>
  <div class="login">
    <h2>Login</h2>
    <form @submit.prevent="login(username, password)">
      <input v-model="username" type="text">
      <input v-model="password" type="password">
      <button type="submit">Zaloguj</button>
    </form>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  name: 'Login',
  data () {
    return {
      username: "",
      password: "",
    }
  },
  methods: {
    login: function(username, password) {
      let user = {
        "username": username,
        "password": password
      }
      Vue.http.post("http://192.168.1.148:8000/user/token/generate", user)
        .then((res) => {
          if (res["error"]) {
            return
          }
          this.$session.start()
          this.$session.set("token", res.data.token)
          Vue.http.headers.common["Authorization"] = "JWT " + res.data.token
          this.$router.push("/")
        })
        .catch((err) => {
          console.log("Error", err)
        })
    }
  }
}
</script>

